/*-----------------------------------------------------------------------------------*/
/*  *.  Related Posts
/*-----------------------------------------------------------------------------------*/

.visualmodo-related-posts {	
  .card-columns {
    margin-bottom: 30px;
    .card { 
      box-shadow: 0 3px 4px 0 rgba(0,0,0,.05);
      transition: 0.3s;
      border: none;
      border-radius: 0;
      &:hover {
        box-shadow: 0 0 10px 0 rgba(0,0,0,.15);
      }
      a { 
        img {
          border-radius: 0;
        }
        .card-body { 
          h6 { 
            color: #222328;
          }
          p{
            margin: 0;
            color: initial;
            line-height: normal;
          }
        }
      }
    }
  }
}