/* Social Icons */

.wp-block-social-links {
  margin: 0 !important;
  li {
    padding: 0 !important;
  }
}

/* Social Icons - Light Mode */

.wrapper-footer.dark .wp-block-social-links .wp-block-social-link svg,
body.light-colors-overlay .header-top .wp-block-social-links .wp-block-social-link svg {
  color: rgba(255,255,255,.9)!important;
}

.wrapper-footer.dark .wp-block-social-links .wp-block-social-link:hover svg,
body.light-colors-overlay .header-top .wp-block-social-links .wp-block-social-link:hover svg {
  color: rgba(255,255,255,.7)!important;
}

/* Social Icons - Dark Mode */

.wrapper-footer.light .wp-block-social-links .wp-block-social-link svg,
body.dark-colors-overlay .header-top .wp-block-social-links .wp-block-social-link svg {
  color: rgba(0,0,0,.9)!important;
}

.wrapper-footer.light .wp-block-social-links .wp-block-social-link:hover svg,
body.dark-colors-overlay .header-top .wp-block-social-links .wp-block-social-link:hover svg {
  color: rgba(0,0,0,.7)!important;
}