.desktop-mode.vertical-nav {
  .hp-slider-revolution > {
    rs-module-wrap {
      margin-left: 300px !important;
      width: calc(100% - 300px) !important;
    }
  }
}

.mobile-mode.vertical-nav {
  .hp-slider-revolution > {
    rs-module-wrap {
      width: 100% !important;
    }
  }
}