/*-----------------------------------------------------------------------------------*/
/*  *.  Mega Menu
/*-----------------------------------------------------------------------------------*/

.desktop-mode {	

  .dropdown.mm {
    &.boxed .dropdown-menu li.dropdown-submenu {
     display: table-cell;
   }
   > ul.dropdown-menu > li > a {
    font-weight: 700;
    background: transparent;
  }
  ul.dropdown-menu li.dropdown-submenu {
    display: inline-block;
    width: 25%;
    padding: 1em;
    margin-right: -0.5%;
    a {
      background-color: transparent;
    }
    > a:after {
      display: none;
    }
    ul.dropdown-menu {
      background-color: transparent;
      position: static !important;
      display: block;
      opacity: 1;
    }
  }
  &.right .dropdown-menu {
    left: 0;
    right: auto;
  }
  &.wide {
    position: static;
    ul.dropdown-menu {
      @extend .container;
      padding: 0 !important;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }
  &.mm-one-full ul.dropdown-menu li.dropdown-submenu {
    width: 100%;
  }
  &.mm-one-half ul.dropdown-menu li.dropdown-submenu {
    width: 50%;
  }
  &.mm-one-third ul.dropdown-menu li.dropdown-submenu {
    width: 33.33333333%;
  }
  &.mm-one-fourth ul.dropdown-menu li.dropdown-submenu {
    width: 25%;
  }
  &.mm-one-sixth ul.dropdown-menu li.dropdown-submenu {
    width: 16.66666667%;
  }
}

.dropdown.sidebar-dropdown-menu ul.dropdown-menu li.dropdown-submenu aside {
	width: 100%;
}

}

/*-----------------------------------------------------------------------------------*/
/*  *.  Mega Menu Vertical
/*-----------------------------------------------------------------------------------*/

.desktop-mode { 

  .vertical-header {

    .dropdown.mm {

      &.wide {
        position: relative;
      }

    }

  }

}

/*-----------------------------------------------------------------------------------*/
/*  *.  Sticky Navigation
/*-----------------------------------------------------------------------------------*/

.sticky-navigation {
  position: sticky;
  z-index: 999;
  top: 0;
}

.desktop-mode.admin-bar .sticky-navigation {
  position: sticky;
  top: 32px;
  z-index: 999;
}

@media screen and (max-width: 600px) {
  .mobile-mode.admin-bar .sticky-navigation {
    top: 0px !important;
  }
}

.mobile-mode {
  &.admin-bar .sticky-navigation {
    position: sticky;
    top: 46px;
  }
  .sticky-navigation {
    position: sticky;
    top: 0px;
  }
}

/*-----------------------------------------------------------------------------------*/
/*  *.  Change Click Submenu to Hover
/*-----------------------------------------------------------------------------------*/

.desktop-mode {
  .dropdown:hover .dropdown-menu {
    display: block;
  }
}

/*-----------------------------------------------------------------------------------*/
/*  *.  Item Description
/*-----------------------------------------------------------------------------------*/

.desktop-mode {
  .header-bottom {
    &.header-large {
      .navbar-nav li a .item-description {
        display: block;
        font-size: 0.8rem;
        line-height: 3em;
        margin-top: -60px;
      }
      .dropdown-menu > li > a .item-description {
        margin: 0;
        line-height: 1em;
        margin-top: 0.5em;
      }
    }
    &.header-medium {
      .navbar-nav li a .item-description {
        display: block;
        font-size: 0.8rem;
        line-height: 3em;
        margin-top: -45px;
      }
      .dropdown-menu > li > a .item-description {
        margin: 0;
        line-height: 1em;
        margin-top: 0.5em;
      }
    }
    &.header-small {
      .navbar-nav li a .item-description {
        display: block;
        font-size: 0.8rem;
        line-height: 3em;
        margin-top: -30px;
      }
      .dropdown-menu > li > a .item-description {
        margin: 0;
        line-height: 1em;
        margin-top: 0.5em;
      }
    }
  }
}

.mobile-mode {
  .item-description {
    display: none;
  }
}

.title-content {
  display: inline-table;
}
/*-----------------------------------------------------------------------------------*/
/*  *.  Icon Menu
/*-----------------------------------------------------------------------------------*/

.icon-title,
.icon-title-description {
  margin-right: 0.5em;
  font-size: 1rem;
}

.desktop-mode {
  nav.navbar-expand-sm.header-bottom .dropdown-menu>li>a {
    display: flex;
    align-items: center;
  }
}

.mobile-mode {
  nav.navbar-expand-sm.header-bottom ul.dropdown-menu:not(.extra-md-menu) {
    background-color: transparent;
  }
}

.desktop-mode {
  nav.navbar-expand-md.header-bottom .dropdown-menu>li>a {
    display: flex;
    align-items: center;
  }
}

.mobile-mode {
  nav.navbar-expand-md.header-bottom ul.dropdown-menu:not(.extra-md-menu) {
    background-color: transparent;
  }
}

.desktop-mode {
  nav.navbar-expand-lg.header-bottom .dropdown-menu>li>a {
    display: flex;
    align-items: center;
  }
}

.mobile-mode {
  nav.navbar-expand-lg.header-bottom ul.dropdown-menu:not(.extra-md-menu) {
    background-color: transparent;
  }
}

.desktop-mode {
  nav.navbar-expand-xl.header-bottom .dropdown-menu>li>a {
    display: flex;
    align-items: center;
  }
}

.mobile-mode {
  nav.navbar-expand-xl.header-bottom ul.dropdown-menu:not(.extra-md-menu) {
