  }
}


/*-----------------------------------------------------------------------------------*/
/*  *.  Extra Items Menu
/*-----------------------------------------------------------------------------------*/

.extra-md-menu {
	width: 25rem;
	padding: 25px !important;
}

/*-----------------------------------------------------------------------------------*/
/*  *.  Multi Level Horizontal Menu
/*-----------------------------------------------------------------------------------*/

.desktop-mode .header-bottom .dropdown:not(.mm).menu-item-has-children .dropdown-menu li.dropdown-submenu:hover > .dropdown-menu {
  display: block;
}

.desktop-mode .header-bottom .dropdown:not(.mm).menu-item-has-children li.dropdown-submenu>.dropdown-menu {
  display: none;
}

.dropdown-submenu {
  position: relative;
}

.mobile-mode li:not(.mm).dropdown.show ul.dropdown-menu,
.mobile-mode li:not(.mm).dropdown.show ul.dropdown-menu li.dropdown-submenu {
  display: block;
}

.mobile-mode .mm .dropdown-submenu.show-submenu > .dropdown-menu {
  display: block;
}

.dropdown-menu {
  box-shadow: none;
  border-radius: 0;
  border: none;
}

.dropdown-submenu {
  > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -6px;
  }
  &:hover {
    > a:after {
      border-left-color: #fff;
    }
  }
  > a:after {
    position: absolute;
    font: normal normal normal 14px/1 FontAwesome;
    content: "\f105";
    right: 1.5rem;
  }
  &.pull-left {
    float: none;
    > .dropdown-menu {
      left: -100%;
      margin-left: 10px;
    }
  }
}

.desktop-mode {
  .dropdown-submenu {
    > .dropdown-menu {
      display: block;
      opacity: 0;
      -webkit-transition: all 200ms ease-in;
      -moz-transition: all 200ms ease-in;
      -ms-transition: all 200ms ease-in;
      -o-transition: all 200ms ease-in;
      transition: all 200ms ease-in;
    }
    &:hover > .dropdown-menu {
      display: block;
      opacity: 1;
    }
  }
  .admin-bar .navbar-fixed-top {
    top: 32px;
  }
}

/*-----------------------------------------------------------------------------------*/
/*  *.  Dropdown Menu Colored
/*-----------------------------------------------------------------------------------*/


//With Background Color Default

nav:not(.navbar-custom) .navbar-nav .dropdown-menu.dropdown-menu-light {
  @extend .bg-light;
  .nav-link {
    color: rgba(0,0,0,.5) !important;
    &:hover, &:focus, &:active {
      color: rgba(0,0,0,.7) !important;
      background-color: transparent;
    }
  }
}

nav:not(.navbar-custom) .navbar-nav .dropdown-menu.dropdown-menu-dark {
  @extend .bg-dark;
  .nav-link {
    color: rgba(255,255,255,.5) !important;
    &:hover, &:focus, &:active {
      color: rgba(255,255,255,.75) !important;
      background-color: transparent;
    }
  }
}

//Without Background Color Default

nav:not(.navbar-custom) .navbar-nav .dropdown-menu.dropdown-menu-light-no-bg .nav-link {
  color: rgba(0,0,0,.5) !important;
  &:hover, &:focus, &:active {
    color: rgba(0,0,0,.7) !important;
    background-color: transparent;
  }
}

nav:not(.navbar-custom) .navbar-nav .dropdown-menu.dropdown-menu-dark-no-bg .nav-link {
  color: rgba(255,255,255,.5) !important;
  &:hover, &:focus, &:active {
    color: rgba(255,255,255,.75) !important;
    background-color: transparent;
  }
}

/*-----------------------------------------------------------------------------------*/
/*  *.  Remove Default Bootstrap Caret of the Menu
/*-----------------------------------------------------------------------------------*/

.dropdown-toggle::after, .woocommerce .variations td.value select::after {
  display: none;
}