/*  16.  Menu
/*-----------------------------------------------------------------------------------*/

/* Cart Content Count */

.cart-content-count.cart-content-count-null {
  visibility: hidden;
}

/* Cart Menu */

.nav-cart {
  span {
    transition: none;
    -moz-transition: none;
    -webkit-transition: none;
  }
  .dropdown-menu {
    position: absolute;
  }
}

.cart-menu {
  ul {
    &.cart_list li, &.product_list_widget li {
      padding: 4px 0;
      margin: 0;
      list-style: none;
    }
    &.cart_list li a, &.product_list_widget li a {
      display: block;
      font-weight: 700;
    }
    &.cart_list, &.product_list_widget {
      list-style: none;
      padding: 0;
      margin: 0;
    }
  }
  div.widget_shopping_cart_content ul.product_list_widget li a {
    line-height: 1 !important;
    height: auto !important;
  }
  a.remove {
    font-size: 1.5em;
    text-align: center;
    border-radius: 100%;
    text-decoration: none;
    border: 0;
    width: 0;
  }
  div.widget_shopping_cart_content p {
    &.woocommerce-mini-cart__empty-message {
      margin: 0;
    }
    &.total {
      padding-top: 10px;
      margin-top: 20px;
      text-align: right;
    }
    &.buttons a.button.wc-forward {
      height: auto;
      line-height: 1;
      padding: 1.2em 0;
      display: block;
      text-align: center;
      margin-top: 2rem;
      font-weight: 700;
      border-radius: 3px;
    }
  }
  ul {
    &.cart_list li {
      &:after, &:before {
        content: " ";
        display: table;
      }
    }
    &.product_list_widget li {
      &:after, &:before {
        content: " ";
        display: table;
      }
    }
    &.cart_list li img, &.product_list_widget li img {
      width: 64px;
      margin-top: -21px;
      float: right;
      height: auto;
      box-shadow: none;
    }
  }
}

.navbar-light .cart-menu,
.nav-cart .dropdown-menu-light-no-bg .cart-menu {
  .woocommerce-mini-cart__empty-message {
    color: rgba(0, 0, 0, 0.5);
  }
  .widget_shopping_cart_content {
    ul.product_list_widget li {
      color: #343a40;
      a {
        color: #343a40 !important;
        transition: all 0.2s ease-in-out;
        &:hover {
          color: rgba(52, 58, 64, 0.3) !important;
        }
        &.remove {
          color: rgba(52, 58, 64, 0.3) !important;
          transition: all 0.2s ease-in-out;
          &:hover {
            color: #343a40 !important;
          }
        }
      }
    }
    p {
      &.total {
        color: #343a40;
        border-top: 1px solid rgba(52, 58, 64, 0.3);
      }
      &.buttons a.button.wc-forward {
        @extend .text-white;
        @extend .bg-secondary;
      }
    }
  }
}

.navbar-dark .cart-menu,
.nav-cart .dropdown-menu-dark-no-bg .cart-menu {
  .woocommerce-mini-cart__empty-message {
    color: rgba(255, 255, 255, 0.5);
  }
  .widget_shopping_cart_content {
    ul.product_list_widget li {
      color: #FFF;
      a {
        color: #FFF !important;
        transition: all 0.2s ease-in-out;
        &:hover {
          color: rgba(255, 255, 255, 0.3) !important;
        }
        &.remove {
          color: rgba(255, 255, 255, 0.3) !important;
          transition: all 0.2s ease-in-out;
          &:hover {
            color: #FFF !important;
          }
        }
      }
    }
    p {
      &.total {
        color: #FFF;
        border-top: 1px solid rgba(255, 255, 255, 0.3);
      }
      &.buttons a.button.wc-forward {
        @extend .text-dark;
        @extend .bg-light;
      }
    }
  }
}
.dropdown-menu.dropdown-menu-dark-no-bg .cart-menu .woocommerce-mini-cart__empty-message {
  color: rgba(255, 255, 255, 0.5);
}


